import Resource from "./resource.js";
export class Sessions extends Resource {
    find = this.makeRequest({
        method: "GET",
    });
    constructor(client) {
        super(client, {
            path: "/admin/realms/{realm}/client-session-stats",
            getUrlParams: () => ({
                realm: client.realmName,
            }),
            getBaseUrl: () => client.baseUrl,
        });
    }
}
